import * as React from "react";
import PropTypes from "prop-types";
import { 
  Radio, 
  RadioGroup, 
  tokens, 
  makeStyles,
  Text
} from "@fluentui/react-components";

const useStyles = makeStyles({
  container: {
    padding: "0 20px 20px 20px",
    backgroundColor: tokens.colorNeutralBackground1,
  },
  title: {
    fontSize: tokens.fontSizeBase400,
    fontWeight: tokens.fontWeightSemibold,
    color: tokens.colorNeutralForeground1,
    marginBottom: "8px",
        textAlign: "center",

  },
  timeColumnsContainer: {
    display: "flex",
    gap: "20px",
  },
  timeColumn: {
    flex: 1,
    display: "flex",
    flexDirection: "column",
  },
  radioGroup: {
    display: "flex",
    flexDirection: "column",
  },
  radioItem: {
    display: "flex",
    alignItems: "center",
  },
  radioLabel: {
    fontSize: tokens.fontSizeBase300,
    color: tokens.colorNeutralForeground1,
    marginLeft: "8px",
    cursor: "pointer",
    flex: 1,
  },
  timeSlot: {
    display: "flex",
    alignItems: "center",
    width: "100%",
  },
  columnTitle: {
    fontSize: tokens.fontSizeBase300,
    fontWeight: tokens.fontWeightSemibold,
    color: tokens.colorNeutralForeground1,
    marginBottom: "8px",
    textAlign: "center",
  },
});

const TimeSelector = ({ 
  selectedStartTime, 
  selectedEndTime,
  onStartTimeSelect,
  onEndTimeSelect,
  timeSlots = []
}) => {
  const styles = useStyles();

  // Generate start times (1-hour intervals from 8:00 AM to 5:00 PM)
  const generateStartTimes = () => {
    const times = [];
    for (let hour = 8; hour <= 17; hour++) {
      const time = new Date();
      time.setHours(hour, 0, 0, 0);
      
      const timeString = time.toLocaleTimeString('en-US', {
        hour: 'numeric',
        minute: '2-digit',
        hour12: true
      });
      
      times.push({
        value: timeString,
        label: timeString,
        time: time
      });
    }
    return times;
  };

  // Generate end times (30-minute intervals from 8:30 AM to 5:30 PM)
  const generateEndTimes = () => {
    const times = [];
    for (let hour = 8; hour <= 17; hour++) {
      for (let minute = 30; minute < 60; minute += 30) {
        if (hour === 17 && minute > 30) break; // Stop at 5:30 PM
        
        const time = new Date();
        time.setHours(hour, minute, 0, 0);
        
        const timeString = time.toLocaleTimeString('en-US', {
          hour: 'numeric',
          minute: '2-digit',
          hour12: true
        });
        
        times.push({
          value: timeString,
          label: timeString,
          time: time
        });
      }
    }
    return times;
  };

  const startTimes = generateStartTimes();
  const endTimes = generateEndTimes();

  return (
    <div className={styles.container}>
      
      <div className={styles.timeColumnsContainer}>
        {/* Start Times Column */}
        <div className={styles.timeColumn}>
          <Text className={styles.columnTitle}>Start Time</Text>
          <RadioGroup
            value={selectedStartTime}
            onChange={(_, data) => onStartTimeSelect(data.value)}
            className={styles.radioGroup}
          >
            {startTimes.map((slot, index) => (
              <div key={index} className={styles.radioItem}>
                <div className={styles.timeSlot}>
                  <Radio 
                    value={slot.value}
                    id={`start-time-${index}`}
                  />
                  <label 
                    htmlFor={`start-time-${index}`}
                    className={styles.radioLabel}
                  >
                    {slot.label}
                  </label>
                </div>
              </div>
            ))}
          </RadioGroup>
        </div>

        {/* End Times Column */}
        <div className={styles.timeColumn}>
          <Text className={styles.columnTitle}>End Time</Text>
          <RadioGroup
            value={selectedEndTime}
            onChange={(_, data) => onEndTimeSelect(data.value)}
            className={styles.radioGroup}
          >
            {endTimes.map((slot, index) => (
              <div key={index} className={styles.radioItem}>
                <div className={styles.timeSlot}>
                  <Radio 
                    value={slot.value}
                    id={`end-time-${index}`}
                  />
                  <label 
                    htmlFor={`end-time-${index}`}
                    className={styles.radioLabel}
                  >
                    {slot.label}
                  </label>
                </div>
              </div>
            ))}
          </RadioGroup>
        </div>
      </div>
    </div>
  );
};

TimeSelector.propTypes = {
  selectedStartTime: PropTypes.string,
  selectedEndTime: PropTypes.string,
  onStartTimeSelect: PropTypes.func.isRequired,
  onEndTimeSelect: PropTypes.func.isRequired,
  timeSlots: PropTypes.arrayOf(PropTypes.shape({
    value: PropTypes.string.isRequired,
    label: PropTypes.string.isRequired,
    time: PropTypes.instanceOf(Date).isRequired,
  })),
};

export default TimeSelector;
