import * as React from "react";
import PropTypes from "prop-types";
import { 
  Button, 
  tokens, 
  makeStyles,
  Spinner
} from "@fluentui/react-components";
import { CalendarAdd24Regular, DocumentAdd24Regular } from "@fluentui/react-icons";

const useStyles = makeStyles({
  container: {
    padding: "0 20px 20px 20px",
    backgroundColor: tokens.colorNeutralBackground1,
  },
  buttonRow: {
    display: "flex",
    gap: "12px",
    width: "100%",
  },
  button: {
    flex: 1,
    height: "48px",
    fontSize: tokens.fontSizeBase400,
    fontWeight: tokens.fontWeightSemibold,
    borderRadius: "8px",
    transition: "all 0.2s ease-in-out",
    boxShadow: `0 2px 4px ${tokens.colorBrandShadow}`,
    "&:hover": {
      transform: "translateY(-2px)",
      boxShadow: `0 4px 8px ${tokens.colorBrandShadow}`,
    },
    "&:active": {
      transform: "translateY(0)",
      boxShadow: `0 2px 4px ${tokens.colorBrandShadow}`,
    },
    "&:disabled": {
      backgroundColor: tokens.colorNeutralBackgroundDisabled,
      color: tokens.colorNeutralForegroundDisabled,
      transform: "none",
      boxShadow: "none",
      cursor: "not-allowed",
    },
  },
  primaryButton: {
    backgroundColor: tokens.colorBrandBackground,
    color: tokens.colorNeutralForegroundOnBrand,
    border: "none",
    "&:hover": {
      backgroundColor: tokens.colorBrandBackgroundHover,
    },
    "&:active": {
      backgroundColor: tokens.colorBrandBackgroundPressed,
    },
  },
  secondaryButton: {
    backgroundColor: tokens.colorNeutralBackground1,
    color: tokens.colorBrandForeground1,
    border: `1px solid ${tokens.colorBrandBackground}`,
    "&:hover": {
      backgroundColor: tokens.colorBrandBackground2,
    },
    "&:active": {
      backgroundColor: tokens.colorBrandBackgroundHover,
    },
  },
  loadingContainer: {
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
    gap: "8px",
  },
});

const AddToCalendarButton = ({ 
  onAddToCalendar,
  isLoading = false,
  disabled = false
}) => {
  const styles = useStyles();

  const handleAddToCalendar = async () => {
    if (onAddToCalendar) {
      await onAddToCalendar();
    }
  };

  return (
    <div className={styles.container}>
      <div className={styles.buttonRow}>
     
        
        <Button
          className={`${styles.button} ${styles.primaryButton}`}
          onClick={handleAddToCalendar}
          disabled={disabled || isLoading}
          icon={isLoading ? undefined : <CalendarAdd24Regular />}
        >
          {isLoading ? (
            <div className={styles.loadingContainer}>
              <Spinner size="tiny" />
              Adding...
            </div>
          ) : (
            "Add to Calendar"
          )}
        </Button>
      </div>
    </div>
  );
};

AddToCalendarButton.propTypes = {
  onAddToCalendar: PropTypes.func.isRequired,
  isLoading: PropTypes.bool,
  disabled: PropTypes.bool,
};

export default AddToCalendarButton;
