import * as React from "react";
import PropTypes from "prop-types";
import { 
  Button, 
  tokens, 
  makeStyles,
  Text
} from "@fluentui/react-components";
import { 
  ChevronLeft24Regular, 
  ChevronRight24Regular 
} from "@fluentui/react-icons";

const useStyles = makeStyles({
  container: {
    padding: "5px 20px 5px 20px",
    backgroundColor: tokens.colorNeutralBackground1,
  },
  title: {
    fontSize: tokens.fontSizeBase400,
    fontWeight: tokens.fontWeightSemibold,
    color: tokens.colorNeutralForeground1,
    marginBottom: "16px",
  },
  weekInfo: {
    fontSize: tokens.fontSizeBase300,
    color: tokens.colorNeutralForeground2,
  },
  dayChips: {
    display: "flex",
    gap: "5px",
    marginBottom: "4px",
    marginTop:"6px",
    flexWrap: "wrap",
  },
  dayChip: {
    minWidth: "80px",
    height: "36px",
    borderRadius: "18px",
    border: `1px solid ${tokens.colorNeutralStroke2}`,
    backgroundColor: tokens.colorNeutralBackground1,
    color: tokens.colorNeutralForeground1,
    fontSize: tokens.fontSizeBase200,
    fontWeight: tokens.fontWeightRegular,
    transition: "all 0.2s ease-in-out",
    "&:hover": {
      backgroundColor: tokens.colorNeutralBackground2,
      transform: "translateY(-1px)",
      boxShadow: `0 2px 4px ${tokens.colorNeutralShadowAmbient}`,
    },
    "&.selected": {
      backgroundColor: `${tokens.colorBrandBackground} !important`,
      color: `${tokens.colorNeutralForegroundOnBrand} !important`,
      borderColor: `${tokens.colorBrandBackground} !important`,
      fontWeight: tokens.fontWeightSemibold,
      transform: "translateY(-1px)",
      boxShadow: `0 4px 8px ${tokens.colorBrandShadow}`,
      "&:hover": {
        backgroundColor: `${tokens.colorBrandBackgroundHover} !important`,
        color: `${tokens.colorNeutralForegroundOnBrand} !important`,
        transform: "translateY(-2px)",
        boxShadow: `0 6px 12px ${tokens.colorBrandShadow}`,
      },
    },
  },
  selectedChip: {
    backgroundColor: `${tokens.colorBrandBackground} !important`,
    color: `${tokens.colorNeutralForegroundOnBrand} !important`,
    borderColor: `${tokens.colorBrandBackground} !important`,
    fontWeight: `${tokens.fontWeightSemibold} !important`,
  },
  navigation: {
    display: "flex",
    justifyContent: "space-between",
    alignItems: "center",
    marginBottom: "6px",
  },
  navButton: {
    backgroundColor: "transparent",
    border: "none",
    color: tokens.colorBrandForeground1,
    fontSize: tokens.fontSizeBase300,
    fontWeight: tokens.fontWeightRegular,
    padding: "8px 8px",
    borderRadius: "6px",
    transition: "all 0.2s ease-in-out",
    display: "flex",
    alignItems: "center",
    gap: "4px",
    "&:hover": {
      backgroundColor: tokens.colorBrandBackground2,
      color: tokens.colorBrandForeground1,
      transform: "translateY(-1px)",
      boxShadow: `0 2px 4px ${tokens.colorNeutralShadowAmbient}`,
    },
    "&:active": {
      transform: "translateY(0)",
      backgroundColor: tokens.colorBrandBackgroundHover,
    },
    "&:last-child svg": {
      marginRight: "-4px",
    },
    "&:first-child svg": {
      marginLeft: "-4px",
    },
  },
  selectedDate: {
    fontSize: tokens.fontSizeBase300,
    color: tokens.colorNeutralForeground2,
    marginBottom: "8px",
    padding: "8px 12px",
    backgroundColor: tokens.colorBrandBackground2,
    borderRadius: "6px",
    border: `1px solid ${tokens.colorBrandBackground}`,
  },
  selectedDateValue: {
    fontSize: tokens.fontSizeBase400,
    fontWeight: tokens.fontWeightSemibold,
    color: tokens.colorBrandForeground1,
    marginLeft:"10px"
  },
});

const WeekSelector = ({ 
  selectedDate, 
  onDateSelect, 
  onPrevWeek, 
  onNextWeek,
  weekDays = []
}) => {
  const styles = useStyles();

  const formatDate = (date) => {
    return date.toLocaleDateString('en-US', { 
      weekday: 'short', 
      day: '2-digit', 
      month: 'short' 
    });
  };

  const formatSelectedDate = (date) => {
    return date.toLocaleDateString('en-US', { 
      day: '2-digit', 
      month: 'short', 
      year: 'numeric' 
    });
  };

  const getWeekStart = (date) => {
    const start = new Date(date);
    const day = start.getDay();
    const diff = start.getDate() - day + (day === 0 ? -6 : 1); // Adjust when day is Sunday
    start.setDate(diff);
    return start;
  };

  const generateWeekDays = (startDate) => {
    const days = [];
    for (let i = 0; i < 5; i++) { // Monday to Friday
      const day = new Date(startDate);
      day.setDate(startDate.getDate() + i);
      days.push(day);
    }
    return days;
  };

  const currentWeekDays = weekDays.length > 0 ? weekDays : generateWeekDays(getWeekStart(selectedDate));

  return (
    <div className={styles.container}>
      <Text className={styles.title}>Book sessions into your calendar</Text>
      <br/>
      <Text className={styles.weekInfo}>
        Week Starting {formatDate(getWeekStart(selectedDate))}
      </Text>

      <div className={styles.dayChips}>
        {currentWeekDays.map((day, index) => (
          <Button
            key={index}
            className={`${styles.dayChip} ${day.toDateString() === selectedDate.toDateString() ? `selected ${styles.selectedChip}` : ''}`}
            onClick={() => onDateSelect(day)}
            style={day.toDateString() === selectedDate.toDateString() ? {
              backgroundColor: tokens.colorBrandBackground,
              color: tokens.colorNeutralForegroundOnBrand,
              borderColor: tokens.colorBrandBackground
            } : {}}
          >
            {formatDate(day)}
          </Button>
        ))}
      </div>

      <div className={styles.navigation}>
        <Button
          className={styles.navButton}
          onClick={onPrevWeek}
        >
                    <ChevronLeft24Regular />

          Prev Week
        </Button>

  
        <Button
          className={styles.navButton}
          onClick={onNextWeek}
        >
          Next Week
          <ChevronRight24Regular />
        </Button>
      </div>

      <div className={styles.selectedDate}>
        <Text>Selected :</Text>
        <Text className={styles.selectedDateValue}>
          {formatSelectedDate(selectedDate)}
        </Text>
      </div>
    </div>
  );
};

WeekSelector.propTypes = {
  selectedDate: PropTypes.instanceOf(Date).isRequired,
  onDateSelect: PropTypes.func.isRequired,
  onPrevWeek: PropTypes.func.isRequired,
  onNextWeek: PropTypes.func.isRequired,
  weekDays: PropTypes.arrayOf(PropTypes.instanceOf(Date)),
};

export default WeekSelector;
