import { PublicClientNext } from "@azure/msal-browser";
import { config } from "../config";

const TOKEN_STORAGE_KEY = "msal_access_token";
const EXPIRATION_STORAGE_KEY = "msal_token_expiration";
const ACCOUNT_STORAGE_KEY = "msal_account"; // Store the user's account info

const msalConfig = {
  auth: {
    clientId: config.CLIENT_ID,
    authority: config.AUTHORITY,
    supportsNestedAppAuth: true,
  },
};

class AccountManager {
  constructor() {
    this.pca = undefined;
    this.initialized = false;
  }

  async initialize() {
    if (this.initialized) {
      return;
    }
    this.pca = await PublicClientNext.createPublicClientApplication(msalConfig);
    this.initialized = true;
  }

  async ensureInitialized() {
    if (!this.initialized) {
      await this.initialize();
    }
  }

  async ssoGetToken(scopes) {
    await this.ensureInitialized();

    const cachedToken = window.localStorage.getItem(TOKEN_STORAGE_KEY);
    const tokenExpiration = window.localStorage.getItem(EXPIRATION_STORAGE_KEY);
    const accountInfo = JSON.parse(window.localStorage.getItem(ACCOUNT_STORAGE_KEY) || 'null'); // Retrieve user account

    // If a valid token is stored and not expired, use it
    if (cachedToken && tokenExpiration && new Date().getTime() < parseInt(tokenExpiration, 10)) {
      console.log("Using cached token.");
      return cachedToken;
    }

    // If token is expired, try silent renewal
    if (accountInfo) {
      try {
        console.log("Trying to renew token silently...");
        const userAccount = await this.pca.acquireTokenSilent({
          scopes,
          account: accountInfo, // Pass the stored account info
        });

        console.log("Token renewed silently.", userAccount.accessToken);
        this.storeToken(userAccount);
        return userAccount.accessToken;
      } catch (silentError) {
        console.warn("Silent renewal failed:", silentError);
      }
    }

    // If silent renewal fails, ask the user to log in
    console.log("Prompting user for interactive login...");
    return this.acquireTokenInteractively(scopes);
  }

  async acquireTokenInteractively(scopes) {
    try {
      const userAccount = await this.pca.acquireTokenPopup({ scopes });
      console.log("Acquired token interactively.",userAccount.accessToken);
      this.storeToken(userAccount);
      return userAccount.accessToken;
    } catch (popupError) {
      console.error("Interactive login failed:", popupError);
      throw new Error("Unable to acquire access token: " + popupError);
    }
  }

  storeToken(userAccount) {
    if (userAccount && userAccount.accessToken) {
      window.localStorage.setItem(TOKEN_STORAGE_KEY, userAccount.accessToken);
      window.localStorage.setItem(EXPIRATION_STORAGE_KEY, (Date.now() + userAccount.expiresIn * 1000).toString());
      window.localStorage.setItem(ACCOUNT_STORAGE_KEY, JSON.stringify(userAccount.account)); // Store account for silent renewal
    }
  }
}

// Create and export a singleton instance
const accountManager = new AccountManager();

export { AccountManager, accountManager };
