import * as React from "react";
import { useState, useCallback, useEffect } from "react";
import { makeStyles, tokens } from "@fluentui/react-components";
import { toast, ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import CalendarHeader from "./CalendarHeader";
import WeekSelector from "./WeekSelector";
import TimeSelector from "./TimeSelector";
import DurationDisplay from "./DurationDisplay";
import AddToCalendarButton from "./AddToCalendarButton";
import { graphService } from "../graphService";

const useStyles = makeStyles({
  container: {
    minHeight: "100vh",
    backgroundColor: tokens.colorNeutralBackground1,
    display: "flex",
    flexDirection: "column",
  },
  content: {
    flex: 1,
    overflowY: "auto",
  },
});

const CalendarApp = () => {
  const styles = useStyles();
  
  // State management
  const [selectedDate, setSelectedDate] = useState(new Date());
  const [selectedStartTime, setSelectedStartTime] = useState("");
  const [selectedEndTime, setSelectedEndTime] = useState("");
  const [isLoading, setIsLoading] = useState(false);
  const [isAuthenticated, setIsAuthenticated] = useState(false);
  const [authLoading, setAuthLoading] = useState(true);

  // Get current week days
  const getWeekStart = (date) => {
    const start = new Date(date);
    const day = start.getDay();
    const diff = start.getDate() - day + (day === 0 ? -6 : 1); // Adjust when day is Sunday
    start.setDate(diff);
    return start;
  };

  const generateWeekDays = (startDate) => {
    const days = [];
    for (let i = 0; i < 5; i++) { // Monday to Friday
      const day = new Date(startDate);
      day.setDate(startDate.getDate() + i);
      days.push(day);
    }
    return days;
  };

  const currentWeekDays = generateWeekDays(getWeekStart(selectedDate));

  // Initialize authentication and set current date
  useEffect(() => {
    const initializeAuth = async () => {
      try {
        setAuthLoading(true);
        const authenticated = await graphService.isAuthenticated();
        setIsAuthenticated(authenticated);
        if (!authenticated) {
          console.log("User not authenticated. Will prompt for login when needed.");
        }
      } catch (error) {
        console.error("Authentication initialization failed:", error);
        setIsAuthenticated(false);
      } finally {
        setAuthLoading(false);
      }
    };

    // Set current date on component mount/refresh
    setSelectedDate(new Date());
    initializeAuth();
  }, []);

  // Form clearing function
  const clearForm = useCallback(() => {
    setSelectedStartTime("");
    setSelectedEndTime("");
    setSelectedDate(new Date()); // Reset to current date
  }, []);

  // Event handlers
  const handleDateSelect = useCallback((date) => {
    setSelectedDate(date);
  }, []);

  const handlePrevWeek = useCallback(() => {
    const newDate = new Date(selectedDate);
    newDate.setDate(selectedDate.getDate() - 7);
    setSelectedDate(newDate);
  }, [selectedDate]);

  const handleNextWeek = useCallback(() => {
    const newDate = new Date(selectedDate);
    newDate.setDate(selectedDate.getDate() + 7);
    setSelectedDate(newDate);
  }, [selectedDate]);

  const handleStartTimeSelect = useCallback((time) => {
    setSelectedStartTime(time);
  }, []);

  const handleEndTimeSelect = useCallback((time) => {
    setSelectedEndTime(time);
  }, []);

  // Helper function to parse times and create date objects
  const parseTimes = useCallback(() => {
    if (!selectedStartTime || !selectedEndTime) {
      return null;
    }

    // Parse the selected start time
    const [startTimeStr, startPeriod] = selectedStartTime.split(' ');
    const [startHours, startMinutes] = startTimeStr.split(':').map(Number);
    let startHour24 = startHours;
    
    if (startPeriod === 'PM' && startHours !== 12) {
      startHour24 += 12;
    } else if (startPeriod === 'AM' && startHours === 12) {
      startHour24 = 0;
    }
    
    // Parse the selected end time
    const [endTimeStr, endPeriod] = selectedEndTime.split(' ');
    const [endHours, endMinutes] = endTimeStr.split(':').map(Number);
    let endHour24 = endHours;
    
    if (endPeriod === 'PM' && endHours !== 12) {
      endHour24 += 12;
    } else if (endPeriod === 'AM' && endHours === 12) {
      endHour24 = 0;
    }
    
    // Create start and end times
    const startTime = new Date(selectedDate);
    startTime.setHours(startHour24, startMinutes, 0, 0);
    
    const endTime = new Date(selectedDate);
    endTime.setHours(endHour24, endMinutes, 0, 0);
    
    // Validate that end time is after start time
    if (endTime <= startTime) {
      throw new Error("End time must be after start time");
    }

    return { startTime, endTime };
  }, [selectedDate, selectedStartTime, selectedEndTime]);

  // Add to current Outlook item (existing functionality)

  // Add to calendar using Microsoft Graph API
  const handleAddToCalendar = useCallback(async () => {
    if (!selectedStartTime || !selectedEndTime) {
      toast.error("Please select both start and end times", {
        position: "bottom-center",
        autoClose: 3000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
      });
      return;
    }

    setIsLoading(true);
    
    try {
      const { startTime, endTime } = parseTimes();
      
      // Create event using Microsoft Graph API
      const result = await graphService.createCalendarEvent({
        startTime: startTime,
        endTime: endTime,
        subject: "Mindx Meetings",
        body: "https://www.mindxmeeting.com/select",
        location: ""
      });
      
      if (result.success) {
        // Show success toast
        toast.success("Event added successfully!", {
          position: "bottom-center",
          autoClose: 3000,
          hideProgressBar: false,
          closeOnClick: true,
          pauseOnHover: true,
          draggable: true,
        });
        
        // Clear the form
        clearForm();
        
        console.log('Event created in calendar successfully:', result.message);
      } else {
        throw new Error(result.message || "Failed to create calendar event");
      }
      
    } catch (error) {
      console.error("Error creating calendar event:", error);
      toast.error(`Failed to create calendar event: ${error.message}`, {
        position: "bottom-center",
        autoClose: 5000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
      });
    } finally {
      setIsLoading(false);
    }
  }, [parseTimes, clearForm]);

 

  return (
    <div className={styles.container}>
      <CalendarHeader 
   
      />
      
      <div className={styles.content}>
        <WeekSelector
          selectedDate={selectedDate}
          onDateSelect={handleDateSelect}
          onPrevWeek={handlePrevWeek}
          onNextWeek={handleNextWeek}
          weekDays={currentWeekDays}
        />
        
        <TimeSelector
          selectedStartTime={selectedStartTime}
          selectedEndTime={selectedEndTime}
          onStartTimeSelect={handleStartTimeSelect}
          onEndTimeSelect={handleEndTimeSelect}
        />
        
        <DurationDisplay 
          duration={
            selectedStartTime && selectedEndTime 
              ? (() => {
                  const [startTimeStr, startPeriod] = selectedStartTime.split(' ');
                  const [startHours, startMinutes] = startTimeStr.split(':').map(Number);
                  let startHour24 = startHours;
                  
                  if (startPeriod === 'PM' && startHours !== 12) {
                    startHour24 += 12;
                  } else if (startPeriod === 'AM' && startHours === 12) {
                    startHour24 = 0;
                  }
                  
                  const [endTimeStr, endPeriod] = selectedEndTime.split(' ');
                  const [endHours, endMinutes] = endTimeStr.split(':').map(Number);
                  let endHour24 = endHours;
                  
                  if (endPeriod === 'PM' && endHours !== 12) {
                    endHour24 += 12;
                  } else if (endPeriod === 'AM' && endHours === 12) {
                    endHour24 = 0;
                  }
                  
                  const startTime = new Date();
                  startTime.setHours(startHour24, startMinutes, 0, 0);
                  
                  const endTime = new Date();
                  endTime.setHours(endHour24, endMinutes, 0, 0);
                  
                  const durationMinutes = Math.round((endTime - startTime) / (1000 * 60));
                  return `${durationMinutes} minutes`;
                })()
              : "Select times"
          } 
        />
        
        <AddToCalendarButton
          onAddToCalendar={handleAddToCalendar}
          isLoading={isLoading}
          disabled={!selectedStartTime || !selectedEndTime}
        />
      </div>
      
      {/* Toast Container */}
      <ToastContainer
        position="bottom-center"
        autoClose={3000}
        hideProgressBar={false}
        newestOnTop={false}
        closeOnClick
        rtl={false}
        pauseOnFocusLoss
        draggable
        pauseOnHover
        theme="light"
      />
    </div>
  );
};

export default CalendarApp;
