/**
 * Microsoft Graph API Service
 * Handles calendar operations using Microsoft Graph API
 */

import { accountManager } from './msal/authConfig';
import { config } from './config';

export class GraphService {
  constructor() {
    this.accessToken = null;
  }

  /**
   * Get access token for Microsoft Graph API
   */
  async getAccessToken() {
    if (!this.accessToken) {
      this.accessToken = await accountManager.ssoGetToken(config.SCOPES);
    }
    return this.accessToken;
  }

  /**
   * Create a calendar event using Microsoft Graph API
   * @param {Object} eventData - Event details
   * @param {Date} eventData.startTime - Event start time
   * @param {Date} eventData.endTime - Event end time
   * @param {string} eventData.subject - Event subject
   * @param {string} eventData.body - Event body/description
   * @param {string} eventData.location - Event location (optional)
   */
  async createCalendarEvent(eventData) {
    try {
      const token = await this.getAccessToken();
      
      // Format dates for Graph API (ISO 8601 format)
      const startDateTime = eventData.startTime.toISOString();
      const endDateTime = eventData.endTime.toISOString();
      
      const eventPayload = {
        subject: eventData.subject || 'Mindx Meetings',
        body: {
          contentType: 'HTML',
          content: eventData.body || 'https://www.mindxmeeting.com/select'
        },
        start: {
          dateTime: startDateTime,
          timeZone: 'UTC'
        },
        end: {
          dateTime: endDateTime,
          timeZone: 'UTC'
        },
        location: eventData.location ? {
          displayName: eventData.location
        } : null,
        isAllDay: false,
        reminderMinutesBeforeStart: 15
      };

      // Remove null location if not provided
      if (!eventPayload.location) {
        delete eventPayload.location;
      }

      const response = await fetch(`${config.GRAPH_ENDPOINT}/me/events`, {
        method: 'POST',
        headers: {
          'Authorization': `Bearer ${token}`,
          'Content-Type': 'application/json'
        },
        body: JSON.stringify(eventPayload)
      });

      if (!response.ok) {
        const errorData = await response.json();
        throw new Error(`Graph API error: ${response.status} - ${errorData.error?.message || 'Unknown error'}`);
      }

      const createdEvent = await response.json();
      
      return {
        success: true,
        message: 'Event created successfully in calendar',
        eventId: createdEvent.id,
        event: createdEvent
      };

    } catch (error) {
      console.error('Error creating calendar event:', error);
      throw new Error(`Failed to create calendar event: ${error.message}`);
    }
  }

  /**
   * Get user's calendar events for a specific date range
   * @param {Date} startDate - Start date
   * @param {Date} endDate - End date
   */
  async getCalendarEvents(startDate, endDate) {
    try {
      const token = await this.getAccessToken();
      
      const startTime = startDate.toISOString();
      const endTime = endDate.toISOString();
      
      const response = await fetch(
        `${config.GRAPH_ENDPOINT}/me/calendar/events?$filter=start/dateTime ge '${startTime}' and end/dateTime le '${endTime}'`,
        {
          method: 'GET',
          headers: {
            'Authorization': `Bearer ${token}`,
            'Content-Type': 'application/json'
          }
        }
      );

      if (!response.ok) {
        const errorData = await response.json();
        throw new Error(`Graph API error: ${response.status} - ${errorData.error?.message || 'Unknown error'}`);
      }

      const data = await response.json();
      return data.value || [];

    } catch (error) {
      console.error('Error fetching calendar events:', error);
      throw new Error(`Failed to fetch calendar events: ${error.message}`);
    }
  }

  /**
   * Check if user is authenticated
   */
  async isAuthenticated() {
    try {
      await this.getAccessToken();
      return true;
    } catch (error) {
      return false;
    }
  }
}

// Export a singleton instance
export const graphService = new GraphService();
