import * as React from "react";
import PropTypes from "prop-types";
import { 
  tokens, 
  makeStyles,
  Text
} from "@fluentui/react-components";

const useStyles = makeStyles({
  container: {
    padding: "0 20px 20px 20px",
    backgroundColor: tokens.colorNeutralBackground1,
  },
  title: {
    fontSize: tokens.fontSizeBase400,
    fontWeight: tokens.fontWeightSemibold,
    color: tokens.colorNeutralForeground1,
    marginBottom: "8px",
  },
  duration: {
    fontSize: tokens.fontSizeBase300,
    color: tokens.colorNeutralForeground2,
    padding: "8px 12px",
    marginLeft:"10px",
    backgroundColor: tokens.colorNeutralBackground2,
    borderRadius: "6px",
    border: `1px solid ${tokens.colorNeutralStroke2}`,
    transition: "all 0.2s ease-in-out",
    "&.hasDuration": {
      backgroundColor: tokens.colorBrandBackground2,
      color: tokens.colorBrandForeground1,
      borderColor: tokens.colorBrandBackground,
      fontWeight: tokens.fontWeightSemibold,
       marginLeft:"10px",
    },
  },
});

const DurationDisplay = ({ 
  duration = "15 minutes"
}) => {
  const styles = useStyles();
  
  // Check if duration is calculated (not default)
  const hasCalculatedDuration = duration !== "Select times" && duration !== "15 minutes";

  return (
    <div className={styles.container}>
      <Text className={styles.title}>Duration</Text>
      <Text className={`${styles.duration} ${hasCalculatedDuration ? 'hasDuration' : ''}`}>
        {duration}
      </Text>
    </div>
  );
};

DurationDisplay.propTypes = {
  duration: PropTypes.string,
};

export default DurationDisplay;
